  /* Header.css */
header {
  background-color: white;
  color: rgb(22, 2, 2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

@media (max-width: 768px) {
  header {
    flex-direction: column;
  }

  header nav {
    margin-top: 1rem;
  }

  header nav ul {
    flex-direction: column;
  }

  header nav ul li {
    margin-right: 0;
    margin-bottom: 0.5rem;
  }
}
  
  header h1 {
    margin: 0;
    font-size: 1.5rem;
  }
  header img {
    height: 6rem; /* Set the height of the logo image */
    margin-right: 1rem;
  }
  
  header nav ul {
    list-style-type: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  header nav ul li {
    margin-right: 1rem;
    text-decoration: none;

  }
  header nav ul li a {
    text-decoration: none;
    color: inherit; /* This will make the link color inherit the color of the parent li element */
    &:hover {
      text-decoration: underline; /* This will add an underline when the link is hovered over */
    }
    &:focus,
    &:active {
      text-decoration: none; /* This will remove the underline when the link is clicked */
    }
  }

  .burgerVisible {
    visibility: hidden;
  }

  
  @media only screen and (max-width: 620px) {
    /* For mobile phones: */
    .burgerVisible {
      visibility: visible;
    }
    .nav {
      visibility: hidden;
    }

    .simplenav{
      visibility: hidden;
    }
  
    .FooterItems {
      text-align: center;
      font-size: 12px;
      margin-top: -10vh;
      padding: 0px 30px 0px 30px;
      color: #d5bdc2;
    }
  
    .FooterIcons {
      text-align: center;
      font-size: 12px;
      margin-top: 3%;
      margin-left: 10px;
      padding: 10px 10px 10px 10px;
      color: #d5bdc2;
    }
  }
  

  .bm-burger-button {
    position: absolute;
    width: 36px;
    height: 30px;
    right: 32px;
    top: 26px;
  }


  .bm-burger-button {
    position: absolute;
    width: 36px;
    height: 30px;
    right: 32px;
    top: 26px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
  
  
